export default {
	data() {
		return {
			isactive:-1,
			result:'',
			page:1,
			pagesize:10,
			datalist:[],
			id:'',
			cont:1,
			content:''
		}
	},
	onLoad(options){
		this.result = options.searchresult
		this.getdata(options.searchresult)
	},
	onReachBottom() {
		this.page = this.page+=1
		console.log(this.page)
		this.getdata()
		this.cont=1
	},
	methods: {
		getimgurl(image){
			return"http://uniapp.ruange.com.cn/"+image
		},
		
		select(index){
			console.log(this.id)
			this.datalist[index].suibian = !this.datalist[index].suibian
			if(this.datalist[index].suibian==true){
				uni.request({
					url:this.common.websiteUrl+"house_index_add",
					header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
					method:"post",
					data:{
						'user_id':this.id,
						'hometype':this.datalist[index].id
					},
					success: (res) => {
						console.log('关联成功')
					}
				});	
			}else{
				uni.request({
					url:this.common.websiteUrl+"house_index_delhome",
					header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
					method:"post",
					data:{
						'user_id':this.id,
						'hometype':this.datalist[index].id
					},
					success: (res) => {
						console.log('关联取消')
					}
				});	
			}
			
			
		},
		searchresults(e){
			this.page = 1
			this.getdata(e.detail.value)
			console.log(e.detail.value)
			this.cont=2
			this.content = e.detail.value
		},
		close(){
			this.content=''
			this.getdata()
		},
		back(){
			uni.navigateTo({
			    url: '../myhousetypesearch/myhousetypesearch'
			});
		},
		myhousetypedetail(index){
			uni.navigateTo({
			    url: '../myhousetypedetail/myhousetypedetail?typeid='+this.datalist[index].id
			});
		},
		getdata(data){
			uni.getStorage({
				key:'userinfo',
				success:(res)=>{
					console.log('进来了')
					this.id=res.data.id
					uni.request({
						url:this.common.websiteUrl+"house_index_index",
						header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
						method:"post",
						data:{
							'search_value':data,
							'page':this.page,
							'pagesize':this.pagesize,
							'user_id':this.id
						},
						success: (res) => {
							if(res.data.code==200){
								// console.log(res.data.data)
								this.datalist = this.datalist.concat(res.data.data)
								console.log(this.datalist)
							}else{
								if(this.cont==1){
									this.callback('暂无更多户型')
								}else{
									this.callback('暂无更多户型')
									this.datalist=[]	
								}
							}
						}
					});
					
				}
			})
			
		},
		callback(value){
			uni.showToast({
				title:value,
				icon:'none'
			})
		},
	}
}
